import Vue from 'vue'
import Router from 'vue-router'
// 处理高版本router的不兼容报错问题
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
import Login from '@/components/login/login'
const dashboard = resolve => require(['../components/dashboard/dashboard'], resolve);
Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      redirect: 'login'
    },
    {
      path: '/dashboard',
      meta: {
        title:'报表主页', // 添加该字段，表示进入这个路由是需要登录的
      },
      component: resolve => require(['../components/common/home'], resolve),
      children: [{
          path: '/', //欢饮页
          meta: {
          title:'报表主页'
          },
          component: resolve => require(['../components/dashboard/dashboard'], resolve)
        },
        {
          path: '/monitoring', //看板
          meta: {
            title:'看板'
          },
          component: resolve => require(['../components/monitoring/monitoring'], resolve)
        },
        {
          path: '/feeder', //站位发料
          meta: {
            title:'站位发料'
          },
          component: resolve => require(['../components/feeder/feeder'], resolve)
        },
        {
          path: '/storage', //仓储
          meta: {
            title:'仓储管理'
          },
          component: resolve => require(['../components/material/storage/storage'], resolve)
        },
        {
          path: '/statistics', // statistics物料统计
          meta: {
            title:'物料统计'
          },
          component: resolve => require(['../components/material/statistics/statistics'], resolve)
        },
        {
          path: '/update', // 物料更新
          meta: {
            title:'物料数量更新'
          },
          component: resolve => require(['../components/material/update/update'], resolve)
        },
        {
          path: '/freeze', // 物料更新
          meta: {
            title:'物料冻结'
          },
          component: resolve => require(['../components/material/freeze/freeze'], resolve)
        },
        {
          path: '/operation', // 呆滞钢网

          meta: {
            title:'操作记录'
          },
          component: resolve => require(['../components/material/operation/operation'], resolve)
        },
        {
          path: '/user', // 用户管理

          meta: {
           title:'用户管理'
          },
          component: resolve => require(['../components/user/user/user'], resolve)
        },
        {
          path: '/trace', // 用户回溯
          meta: {
            title:'用户回溯'
          },
          component: resolve => require(['../components/user/trace/trace'], resolve)
        },
        {
          path: '/materialshelf',
          meta: {
            title:'物料架配置'
          },
          component: resolve => require(['../components/materialshelf/materialshelf/materialshelf'], resolve)
        },
        {
          path: '/warehouseconfig',
          meta: {
           title:'库位配置'
          },
          component: resolve => require(['../components/materialshelf/warehouseconfig/warehouseconfig'],
            resolve)
        },
        {
          path: '/serverconfig',
          meta: {
            title:'服务器配置'
          },
          component: resolve => require(['../components/serverconfig/serverconfig'], resolve)
        },
        {
          path: '/apirecord',
          meta: {
            title:'API记录'
          },
          component: resolve => require(['../components/apirecord/apirecord'], resolve)
        },
        {
          path: '/order',
          meta: {
            title:'工单列表'
          },
          component: resolve => require(['../components/ordermanagement/order/order'], resolve)
        },
        // {
        //   path: '/ordertrace/:id',
        //   meta: {
        //     btnPermissions: ['admin', 'supper']
        //   },
        //   component: resolve => require(['../components/ordermanagement/ordertrace/ordertrace'], resolve)
        // },
        {
          path: '/bomfile',
          meta: {
            title:'取料表文件'
          },
          component: resolve => require(['../components/file/bomfile/bomfile'], resolve)
        },
        {
          path: '/feederfile',
          meta: {
            title:'站位表文件'
          },
          component: resolve => require(['../components/file/feederfile/feederfile'], resolve)
        }
      ]
    },
    {
      path: '/pdasearch',
      meta: {
        requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
      },
      component: resolve => require(['../components/views/common/home'], resolve),
      children: [{
          path: '/', //pda搜索
          component: resolve => require(['../components/views/pda/pdasearch'], resolve)
        },
        {
          path: '/pdaout', //取料
          component: resolve => require(['../components/views/pda/pdaout'], resolve)
        },
        {
          path: '/pdain', //入料
          component: resolve => require(['../components/views/pda/pdain'], resolve)
        },
        {
          path: '/aboutme', //入料
          component: resolve => require(['../components/views/pda/aboutme'], resolve)
        },
        {
          path: '/detail', //详情页
          component: resolve => require(['../components/views/pda/detail'], resolve)
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      meta: {
        btnPermissions: ['admin', 'supper']
      },
      component: resolve => require(['../components/login/login'], resolve),
    },
    {
      path: '/pdalogin',
      name: 'pdaLogin',
      component: resolve => require(['../components/views/login/login'], resolve),
    },
  ]
})



