export function timeConvert(timestamp, num) { //num:0 YYYY-MM-DD  num:1  YYYY-MM-DD hh:mm:ss // timestamp:时间戳 
  timestamp = timestamp + '';
  timestamp = parseInt(timestamp * 1000);
  var date = new Date(timestamp);
  var y = date.getFullYear();
  var m = date.getMonth() + 1;
  m = m < 10 ? ('0' + m) : m;
  var d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  var h = date.getHours();
  h = h < 10 ? ('0' + h) : h;
  var minute = date.getMinutes();
  var second = date.getSeconds();
  minute = minute < 10 ? ('0' + minute) : minute;
  second = second < 10 ? ('0' + second) : second;
  if (num == 0) {
    return y + '-' + m + '-' + d;
  } else {
    return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
  }
}
// csv文件转码
export function genUrl(encoded, options) {
  const dataBlob = new Blob([`\ufeff${encoded}`], {
    type: 'text/plain;charset=utf-8'
  }); //返回的格式
  return window.URL.createObjectURL(dataBlob);
}

// 获取当前日期
export function localDate() {
  var date = new Date();
  var dateObj = new Object;
  dateObj.year = date.getFullYear();
  dateObj.month = date.getMonth() + 1;
  dateObj.month = dateObj.month < 10 ? ('0' + dateObj.month) : dateObj.month;
  dateObj.day = date.getDate();
  dateObj.day = dateObj.day < 10 ? ('0' + dateObj.day) : dateObj.day;
  return dateObj;
}

// 判断当前设备是否为pc还是手机或者ipad
export function deviceModel() {
  var os = function() {
    var ua = navigator.userAgent,
      isWindowsPhone = /(?:Windows Phone)/.test(ua),
      isSymbian = /(?:SymbianOS)/.test(ua) || isWindowsPhone,
      isAndroid = /(?:Android)/.test(ua),
      isFireFox = /(?:Firefox)/.test(ua),
      isChrome = /(?:Chrome|CriOS)/.test(ua),
      isTablet = /(?:iPad|PlayBook)/.test(ua) || (isAndroid && !/(?:Mobile)/.test(ua)) || (isFireFox && /(?:Tablet)/.test(
        ua)),
      isPhone = /(?:iPhone)/.test(ua) && !isTablet,
      isPc = !isPhone && !isAndroid && !isSymbian;
    return {
      isTablet: isTablet,
      isPhone: isPhone,
      isAndroid: isAndroid,
      isPc: isPc
    };
  }();
  if (os.isAndroid || os.isPhone) {
    return 'isMobile'
  } else if (os.isTablet) {
    return 'isPad'
  } else if (os.isPc) {
    return 'isPc'
  }
}
// 按钮级别权限控制
export function permission() {
  let user_group_power = localStorage.getItem('user_group_power')
  if (user_group_power >= 2) {
    return true
  } else {
    return false
  }
}
//超管权限控制
export function surpermission() {
  let user_group_power = localStorage.getItem('user_group_power')
  if (user_group_power <= 1) {
    return true
  } else {
    return false
  }
}
//按钮防抖
export const Debounce = (fn, t) => {
  let delay = t || 500
  let timer
  return function() {
    let args = arguments;
    if (timer) {
      clearTimeout(timer)
    }

    let callNow = !timer

    timer = setTimeout(() => {
      timer = null
    }, delay)

    if (callNow) fn.apply(this, args)
  }
}



