import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
 
const store = new Vuex.Store({
 
  state: {
    // 存储token
    user_name:localStorage.getItem('user_name') ? localStorage.getItem('user_name') : '',
    user_id:localStorage.getItem('user_id') ? localStorage.getItem('user_id') : '',
    user_group_power:localStorage.getItem('user_group_power') ? localStorage.getItem('user_group_power') : '',
    isCollapse:false ,
    color:3,
    pdacolor:3,
    itemCode:1,
    row:''
  },
 
  mutations: {
    // 修改不同用户登入信息，并将token存入localStorage
    changeLogin (state, user) {
      state.user_name = user.user_name;
      state.user_group_power = user_data.user_group_power;
    },
    //修改isCollapse改变菜单的收缩
    changeCollapse (state ,collapseState) {
      state.isCollapse =!collapseState.isCollapse
      // console.log(state.isCollapse)
    },
    changeColor(state,color){
      state.color = color.color
    },
    // pda指示灯取色
    pdaColor(state,pdacolor){
      state.pdacolor = pdacolor.pdacolor
    },
     // pda菜单切换下状态的更改
    changeTab(state,itemCode){
      state.itemCode = itemCode.itemCode
    },
    // pda取出row传值
    changeRow(state,row){
      state.row = row.row
    }
  },
});
 
export default store;


